.add-another-link { font-size: 12px; text-decoration: underline; }

.logo-button-usps { position: relative; cursor: pointer; border: 1px solid #9b9b9b; padding: 15px; border-radius: 0; color: #000; width: 160px; height: 120px; background: #fff url("../images/logo-usps.png") center center no-repeat; transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; }
.logo-button-usps:active, .logo-button-usps.active { border-width: 2px; border-color: #3e53a4; }
.logo-button-usps.disabled { background-image: url("../images/logo-usps-disabled.png"); cursor: not-allowed; }
.logo-button-usps.disabled:hover { border: 1px solid #9b9b9b; box-shadow: none; }

.item-selector-button { position: relative; text-align: center; cursor: pointer; border: 1px solid #9b9b9b; padding: 15px; border-radius: 0; color: #000; width: 160px; height: 120px; transition: all ease-in-out .1s, box-shadow ease-in-out .1s; }
.item-selector-button .title { color: #3e53a4; font-size: 12px; margin: 0; padding-top: -3px; font-family: "PrecisionSans_W_Md", "Helvetica Neue", Arial, sans-serif; }
.item-selector-button .divider { height: 1px; width: 20px; background-color: #9b9b9b; margin: 4px auto 10px; }
.item-selector-button .image { background: #fff url("../images/box.png") center center no-repeat; width: 64px; height: 57px; margin: 4px auto; }
.item-selector-button:hover, .item-selector-button.hover { padding: 14px; }
.item-selector-button:hover:after, .item-selector-button.hover:after { content: ''; position: absolute; bottom: -13px; left: 66px; border-width: 14px 14px 0; border-style: solid; border-color: #fff transparent; }
.item-selector-button:hover:before, .item-selector-button.hover:before { content: ''; position: absolute; bottom: -15px; left: 66px; border-width: 14px 14px 0; border-style: solid; border-color: #3e53a4 transparent; }
.item-selector-button:active, .item-selector-button.active { border-width: 2px; border-color: #3e53a4; background-color: #3e53a4; }
.item-selector-button:active:before, .item-selector-button.active:before { content: ''; position: absolute; bottom: -15px; left: 66px; border-width: 14px 14px 0; border-style: solid; border-color: #3e53a4 transparent; }
.item-selector-button:active .title, .item-selector-button.active .title { color: #fff; }
.item-selector-button:active .divider, .item-selector-button.active .divider { background-color: #fff; }
.item-selector-button:active .image, .item-selector-button.active .image { background-color: #3e53a4; }
.item-selector-button:active:hover, .item-selector-button.active:hover { padding: 15px; box-shadow: none; }
